import { EventTarget } from '../EventTarget.js';
import { Utils } from '../Utils.js';
import { Color } from '../Color.js';
import { ColorSlider } from '../ColorSlider/ColorSlider.js';
import { AlphaSlider } from '../AlphaSlider/AlphaSlider.js';
import { ColorArea } from '../ColorArea/ColorArea.js';
import './ColorPicker.css';

class ColorPicker extends EventTarget {
    constructor (container, options) {
        super();
        this._container = container;
        this._options = options || {};
        this._options.colorMode = this._options.colorMode || 'hex';
        this._container.classList.add('color-picker');
        this._container.innerHTML = [
            '<table>',
                '<tbody>',
                    '<tr>',
                        '<td>',
                            '<span class="color-picker-sample"></span>',                            
                        '</td>',
                        '<td>',
                            '<span class="color-picker-mode"></span>',
                            '<input type="text" class="color-picker-box" />',
                        '</td>',
                        '<td>',
                            '<span class="color-picker-box-button"></span>',
                        '</td>',
                        '<td>',
                        '</td>',
                    '</tr>',
                    '<tr>',
                        '<td colspan="3">',
                            '<div class="color-picker-area"></div>',
                        '</td>',
                        '<td>',
                            '<div class="color-picker-alpha"></div>',
                        '</td>',
                    '</tr>',
                    '<tr>',
                        '<td colspan="3">',
                            '<div class="color-picker-slider"></div>',
                        '</td>',
                        '<td>',
                        '</td>',
                    '</tr>',
                '</tbody>',
            '</table>'
        ].join('');
        
        this._preventDefault = this._preventDefault.bind(this);
        this._stopPropagation = this._stopPropagation.bind(this);
        this._container.addEventListener('click', this._preventDefault);
        this._container.addEventListener('click', this._stopPropagation);
        this._container.addEventListener('dragstart', this._preventDefault);
        this._container.addEventListener('dragstart', this._stopPropagation);
        // this._container.addEventListener('mousemove', this._preventDefault);;
        // this._container.addEventListener('mousemove', this._stopPropagation);

        this._sample = this._container.querySelector('.color-picker-sample');
        this._box = this._container.querySelector('.color-picker-box');
        this._changeBox = this._changeBox.bind(this);
        this._box.addEventListener('change', this._changeBox);
        this._box.addEventListener('focus', e => this._box.select());
        this._avalue = this._container.querySelector('.color-picker-alpha-value');
        this._area = new ColorArea (this._container.querySelector('.color-picker-area'));
        this._alpha = new AlphaSlider (this._container.querySelector('.color-picker-alpha'), {
            orientation: 'vertical', order: 'desc', useTip: true
        });
        this._slider = new ColorSlider (this._container.querySelector('.color-picker-slider'));
        this._changeColor = this._changeColor.bind(this);
        this._area.addEventListener ('change', this._changeColor);
        this._changeHue = this._changeHue.bind(this);
        this._slider.addEventListener('change', this._changeHue);
        this._changeAlpha = this._changeAlpha.bind(this);
        this._alpha.addEventListener('change', this._changeAlpha);
        this._toggleColorMode = this._toggleColorMode.bind(this);
        this._container.querySelector('.color-picker-box-button').addEventListener ('click', this._toggleColorMode);
        this._mode = this._container.querySelector('.color-picker-mode');
        this._initialize();        
    }
    _preventDefault (e) {
        e.preventDefault();
    }
    _stopPropagation (e) {
        e.stopPropagation();
    }
    _initialize () {
        var value = this._slider.value;
        this._alpha.setRGB(value.rgb);
        this._area.setHue (value.hsl.h);
        var hsl = this._area.hsl;        
        var color = Color.hsl2rgb(hsl.h, hsl.s, hsl.l);
        var r = color.r;
        var g = color.g;
        var b = color.b;
        this._setBox(r, g, b);
    }
    setColorMode (mode) {
        this._options.colorMode = mode;
        var hsl = this._area.hsl;        
        var color = Color.hsl2rgb(hsl.h, hsl.s, hsl.l);
        var r = color.r;
        var g = color.g;
        var b = color.b;
        this._setBox(r, g, b);        
    }
    _changeBox (e) {
        switch (this._options.colorMode) {
            case 'rgb':
                this.value = Color.rgb2hex (this._box.value);
                break;
            case 'hex':
                this.value = this._box.value;
                break;
            default:
                break;
        }         
    }
    _toggleColorMode (e) {        
        switch (this._options.colorMode) {
            case 'rgb':
                this._options.colorMode = 'hex';
                break;
            default:
            case 'hex':
                this._options.colorMode = 'rgb';
                break;
        }
        var hsl = this._area.hsl;
        var color = Color.hsl2rgb(hsl.h, hsl.s, hsl.l);
        var r = color.r;
        var g = color.g;
        var b = color.b;
        this._setBox(r, g, b);
    }
    // rgb (255,255,255)
    _setBox (r, g, b) {
        var value = '';
        switch (this._options.colorMode) {
            case 'rgb':
                value = [r, g, b].join(',');
                this._mode.innerText = 'rgb';
                this._sample.style.backgroundColor ='rgb(' + value  + ')';
                break;
            default:
            case 'hex':
                value = Color.rgb2hex(r, g, b);
                this._mode.innerText = 'hex';
                this._sample.style.backgroundColor = value;
                break;
        }        
        this._box.value = value;
    }
    _changeColor (e) {
        var hsl = e.detail;
        var color = Color.hsl2rgb(hsl.h, hsl.s, hsl.l);
        var r = color.r;
        var g = color.g;
        var b = color.b;
        this._setBox(r, g, b);
        var a = this._alpha.value;

        // this.dispatchEvent (new CustomEvent('change', {detail: {hsl: hsl, rgb: {r: r, g: g, b: b, a: a}, hex: Color.rgb2hex(r,g,b)}}));

        let event = document.createEvent('Event');
        event.initEvent('change', false, false);
        event.detail = {hsl: hsl, rgb: {r: r, g: g, b: b, a: a}, hex: Color.rgb2hex(r,g,b)};
        this.dispatchEvent(event);
    }
    _changeHue (e) {
        this._alpha.setRGB (e.detail.rgb);
        this._area.setHue (e.detail.hsl.h);
    }
    _changeAlpha (e) {
        this._area.setAlpha(e.detail.value);
    }
    get value () {
        var hsl = this._area.hsl;
        var color = Color.hsl2rgb(hsl.h, hsl.s, hsl.l);
        var r = color.r;
        var g = color.g;
        var b = color.b;
        var a = this._alpha.value;
        return {rgb: {r,g,b,a}, hsl};
    }
    set value (hex) {
        let {r,g,b} = Color.hex2rgb (hex);
        this._slider.value = {r,g,b};
        this._area.hsl = Color.rgb2hsl (r, g, b);
        this._alpha.setRGB ({r,g,b});
    }
}
    
export { ColorPicker };


