import { Slider } from '../Slider/Slider.js';
import { Color } from '../Color.js';
import './ColorSlider.css';

class ColorSlider extends Slider {
    constructor (container, options = {}) {
        super(container, options);
        this._options.alpha = this._options.alpha || 1.0;
        this._options.hue = this._options.hue || 0;
        this._options.saturation = this._options.saturation || 1.0;
        this._options.lightness = this._options.lightness || 0.5;
        this._container.classList.add('color-slider');        
        this._canvas = document.createElement('canvas');
        this._container.querySelector('.slider-bar').appendChild(this._canvas);                
        switch (this._options.orientation) {
            case 'horizontal':
                this._renderHorizontal(this._options.alpha);
                break;
            case 'vertical':
                this._renderVertical(this._options.alpha);
                break;
        }         
        this._handleChange = this._handleChange.bind(this);
        this.addEventListener('slide', this._handleChange);        
    }    
    _handleChange (e) {        
        this._options.hue = Math.round (e.detail.value * 360 /  (this._options.orientation === 'horizontal' ? this._width : this._height));      

        // this.dispatchEvent (new CustomEvent('change', {detail: this.value}));

        let event = document.createEvent('Event');
        event.initEvent('change', false, false);
        event.detail = this.value;
        this.dispatchEvent(event);
    }
    _renderHorizontal (alpha) {
        var ctx = this._canvas.getContext('2d');        
        var imgData = ctx.createImageData (this._canvas.width, this._canvas.height);
        var data = imgData.data;
        for (var i = 0; i < data.length; i += 4) {
            var h = ((i / 4) % imgData.width) * 360 / imgData.width;
            var color = Color.hsl2rgb(h, this._options.saturation, this._options.lightness);
            var r = color.r;
            var g = color.g;
            var b = color.b;
            var a = Math.round (alpha * 255);
            data[i + 0] = r;
            data[i + 1] = g;
            data[i + 2] = b;
            data[i + 3] = a;
        }        
        ctx.putImageData (imgData, 0, 0);
    }
    _renderVertical (alpha) {
        var ctx = this._canvas.getContext('2d');       
        var imgData = ctx.createImageData (this._canvas.width, this._canvas.height);
        var data = imgData.data;
        for (var i = 0; i < data.length; i += 4) {
            var h = Math.floor ((i / 4) / imgData.width) * 360 / imgData.height;
            var color = Color.hsl2rgb(h, this._options.saturation, this._options.lightness);
            var r = color.r;
            var g = color.g;
            var b = color.b;
            var a = Math.round (alpha * 255);
            data[i + 0] = r;
            data[i + 1] = g;
            data[i + 2] = b;
            data[i + 3] = a;            
        }
        ctx.putImageData (imgData, 0, 0);
    }
    // a (1)
    set alpha (a) {
        this._options.alpha = a;
        switch (this._options.orientation) {
            case 'horizontal':
                this._renderHorizontal(this._options.alpha);
                break;
            case 'vertical':
                this._renderVertical(this._options.alpha);
                break;
        }       
    }
    // rgb (255,255,255), hsl(360,1,1)
    get value () {
        var color = Color.hsl2rgb(this._options.hue, this._options.saturation, this._options.lightness);
        var r = color.r;
        var g = color.g;
        var b = color.b;
        return {
            rgb: {
                r: r,
                g: g,
                b: b
            },
            hsl: {
                h: this._options.hue,
                s: this._options.saturation,
                l: this._options.lightness
            }
        };
    }
    set value ({r, g, b}) {        
        let { h } = Color.rgb2hsl (r, g, b);
        this._options.hue = h;
        super.value = Math.round ((this._options.orientation === 'horizontal' ? this._width : this._height) * h / 360);
    }
}      
    
export { ColorSlider };


