import { Utils } from './Utils.js';

class Color {
    hsl2rgb (h, s, l) {
        var q;
        if (l < 0.5) {
            q =  l * (1.0 + s);
        }
        else if (l >= 0.5) {
            q = l + s - l * s;
        }
        var p = 2.0 * l - q;
        var hk = h / 360;
        var norm = function (tc) {
            if (tc < 0) return tc + 1.0;
            if (tc > 1) return tc - 1.0;
            return tc;
        }
        var tr = norm (hk + 1 / 3);
        var tg = norm (hk);
        var tb = norm (hk - 1 / 3);

        var color = function (tc) {
            if (tc < 1 / 6) {
                return p + ((q - p) * 6.0 * tc);
            }
            if (1 / 6 <= tc && tc < 1 / 2) {
                return q;
            }
            if (1 / 2 <= tc && tc < 2 / 3) {
                return p + ((q- p) * (2 / 3 - tc) * 6.0);
            }
            return p;
        };
        return {
            r: Math.round (color (tr) * 255),
            g: Math.round (color (tg) * 255),
            b: Math.round (color (tb) * 255)
        };
    }
    rgb2hsl (R, G, B) {
        var r = R / 255, g = G / 255, b = B / 255;
        var max = Math.max (r, g, b);
        var min = Math.min (r, g, b);
        var h;
        if (max == min) {
            h = undefined;
        }
        else if (max == r && g >= b) {
            h = 60 * (g - b) / (max - min);
        }
        else if (max == r && g < b) {
            h = 60 * (g - b) / (max - min) + 360;
        }
        else if (max == g) {
            h = 60 * (b - r) / (max - min)  + 120;
        }
        else if (max == b) {
            h = 60 * (r - g) / (max - min) + 240;
        }
        var l = (max + min) / 2;
        var s;
        if (l == 0 || max == min) {
            s = 0;
        }
        else if (0 < l && l <= 0.5) {
            s = (max - min) / (max + min);
        }
        else if (0.5 < l && l < 1) {
            s = (max - min) / (2 - (max + min));
        }
        return {h: h, s: s, l: l};
    }
    rgb2hex (r, g, b) {
        return `#${[r, g, b].map(x => Utils.padLeft (x.toString(16), '0', 2).toUpperCase()).join('')}`;
    }
    hex2rgb (hex) {
        let [r,g,b] = /#([0-9a-f]{2})([0-9a-f]{2})([0-9a-f]{2})/gi.exec(hex).slice(1).map(x => parseInt(x, 16));
        return {r,g,b};
    }
}    
    
let color = new Color ();

export {color as Color};


