class Utils {
    expand (n, a, radix) {
        if (n === 0) {
            return a;
        }
        else {
            return expand (Math.round(n / radix), a.concat(n % radix), radix);
        }
    }
    padLeft (str, sym, width) {
        return this.pad ('left', str, width, sym);
    }
    padRight (str, sym, width) {
        return this.pad ('right', str, width, sym);
    }
    stringToArray (str) {
        let arr = [];
        for (let i = 0; i < str.length; ++i) {
            arr.push (str.charAt(i));
        }
        return arr;
    }
    pad (origin, str, width, sym) {
        var s = this.stringToArray (str);
        for (var i = 0; s.length < width; ++i) {
            if (origin === 'left') {
                s.splice (0, 0, sym);
            }
            else {
                s.push (sym);
            }
        }
        return s.join('');
    }
}

let utils = new Utils();

export {utils as Utils};


